h1 {
    font-size:3.2em;
  }
  
  .header {
    display:block;
    position:relative;
    background: $color-bdark; /* fallback for old browsers */
    background: -webkit-linear-gradient(to left, $color-bdark , $color-blight); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to left, $color-bdark , $color-blight); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */ 
    display:flex;
    justify-content:center;
    align-items:center;
    flex-flow:column nowrap;  
    color:white;
    height: 75vh;
    overflow: hidden;
   
    min-height:600px;
  }
  .header .waves {
    position:absolute;
    bottom:0;
    left:0;
    right:0;  
  }
  
  // Particles
  .particles {
    position:absolute;
    top:-100px;
    left: 10px;
    right: 10px;
    height: 1100px;
  }
  .particles {
      left: 10px;
      right: 10px;
      height: 1100px
  }
  
  .particles .particle {
      position: absolute;
      opacity: .1;
      background: #fff;
      -webkit-animation-iteration-count: infinite!important;
      animation-iteration-count: infinite!important
  }
  
  .particle:nth-child(1) {
      height: 25px;
      width: 25px;
      border-radius: 100%;
      -webkit-animation: floating 28s linear;
      animation: floating 28s linear;
      top: 65%;
      left: 10%
  }
  
